// src/pages/api/submit.js
import { supabase } from "../../../lib/supabase";
import nodemailer from "nodemailer";
import { google } from "googleapis";
import { Readable } from "stream";
import { questions } from "@/data/questions";

export const config = {
    api: {
        bodyParser: {
            sizeLimit: "5mb",
        },
    },
};

export default async function handler(req, res) {
    if (req.method !== "POST") {
        return res.status(405).json({ error: "Method not allowed" });
    }

    try {
        const { fullName, email, phone, linkedin, answers, resume, coverLetter } = req.body;

        console.log("Received data:", {
            fullName,
            email,
            phone,
            linkedin,
            answers,
            resume: resume ? "present" : "none",
            coverLetter: coverLetter ? "present" : "none",
        });

        if (!fullName || !email) {
            return res.status(400).json({ error: "Full name and email are required" });
        }

        // Check for existing candidate by email
        console.log("Checking for existing candidate with email:", email);
        const { data: existingCandidate, error: fetchError } = await supabase
            .from("candidates")
            .select("id")
            .eq("email", email)
            .single();

        let userId;

        if (fetchError && fetchError.code !== "PGRST116") { // PGRST116 = no rows found
            console.error("Fetch existing candidate error:", fetchError);
            return res.status(500).json({ error: "Error checking existing candidate", details: fetchError.message });
        }

        if (existingCandidate) {
            // Update existing candidate
            console.log("Updating existing candidate with email:", email);
            const { data: updatedCandidate, error: updateError } = await supabase
                .from("candidates")
                .update({ full_name: fullName, phone, linkedin })
                .eq("email", email)
                .select()
                .single();

            if (updateError) {
                console.error("Update candidate error:", updateError);
                return res.status(403).json({ error: "Failed to update candidate", details: updateError.message });
            }
            userId = updatedCandidate.id;
            console.log("Updated candidate ID:", userId);
        } else {
            // Insert new candidate
            console.log("Attempting to insert into candidates with data:", { full_name: fullName, email, phone, linkedin });
            const { data: newCandidate, error: insertError } = await supabase
                .from("candidates")
                .insert([{ full_name: fullName, email, phone, linkedin }])
                .select()
                .single();

            if (insertError) {
                console.error("Insert candidate error:", insertError);
                return res.status(403).json({ error: "Failed to insert candidate", details: insertError.message });
            }
            userId = newCandidate.id;
            console.log("Inserted candidate ID:", userId);
        }

        const score = calculateScore(answers);

        // Set up Google Drive API
        let auth;
        try {
            const serviceAccountCreds = JSON.parse(process.env.GOOGLE_SERVICE_ACCOUNT);
            auth = new google.auth.GoogleAuth({
                credentials: serviceAccountCreds,
                scopes: ["https://www.googleapis.com/auth/drive.file"],
            });
        } catch (error) {
            console.error("Google Auth Error:", error.message);
            return res.status(500).json({ error: "Failed to authenticate with Google Drive", details: error.message });
        }

        const drive = google.drive({ version: "v3", auth });

        let resumeUrl = null;
        let coverLetterUrl = null;

        // Upload resume to Google Drive
        if (resume) {
            try {
                console.log("Processing resume upload...");
                const isPdf = resume.startsWith("JVBERi0");
                const resumeExt = isPdf ? "pdf" : "docx";
                const resumeFileName = `${userId}-resume-${Date.now()}.${resumeExt}`;
                const resumeBuffer = Buffer.from(resume, "base64");
                console.log("Resume size (bytes):", resumeBuffer.length);

                const bufferStream = new Readable();
                bufferStream.push(resumeBuffer);
                bufferStream.push(null);

                const fileMetadata = {
                    name: resumeFileName,
                    parents: [process.env.GOOGLE_DRIVE_FOLDER_ID],
                };

                const media = {
                    mimeType: isPdf ? "application/pdf" : "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                    body: bufferStream,
                };

                const driveResponse = await drive.files.create({
                    requestBody: fileMetadata,
                    media,
                    fields: "id, webContentLink",
                });

                await drive.permissions.create({
                    fileId: driveResponse.data.id,
                    requestBody: {
                        role: "reader",
                        type: "anyone",
                    },
                });

                resumeUrl = driveResponse.data.webContentLink;
                console.log("Resume uploaded successfully:", resumeUrl);
            } catch (uploadError) {
                console.error("Resume upload error:", uploadError.message, uploadError.stack || "No stack trace");
                resumeUrl = null;
            }
        }

        // Upload cover letter to Google Drive
        if (coverLetter) {
            try {
                console.log("Processing cover letter upload...");
                const isPdf = coverLetter.startsWith("JVBERi0");
                const coverLetterExt = isPdf ? "pdf" : "docx";
                const coverLetterFileName = `${userId}-cover-letter-${Date.now()}.${coverLetterExt}`;
                const coverLetterBuffer = Buffer.from(coverLetter, "base64");
                console.log("Cover letter size (bytes):", coverLetterBuffer.length);

                const bufferStream = new Readable();
                bufferStream.push(coverLetterBuffer);
                bufferStream.push(null);

                const fileMetadata = {
                    name: coverLetterFileName,
                    parents: [process.env.GOOGLE_DRIVE_FOLDER_ID],
                };

                const media = {
                    mimeType: isPdf ? "application/pdf" : "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
                    body: bufferStream,
                };

                const driveResponse = await drive.files.create({
                    requestBody: fileMetadata,
                    media,
                    fields: "id, webContentLink",
                });

                await drive.permissions.create({
                    fileId: driveResponse.data.id,
                    requestBody: {
                        role: "reader",
                        type: "anyone",
                    },
                });

                coverLetterUrl = driveResponse.data.webContentLink;
                console.log("Cover letter uploaded successfully:", coverLetterUrl);
            } catch (uploadError) {
                console.error("Cover letter upload error:", uploadError.message, uploadError.stack || "No stack trace");
                coverLetterUrl = null;
            }
        }

        // Upsert response data
        console.log("Attempting to upsert response with data:", {
            user_id: userId,
            answers: JSON.stringify(answers),
            score,
            resume_url: resumeUrl,
            cover_letter_url: coverLetterUrl,
        });
        const { error: responseError } = await supabase
            .from("responses")
            .upsert(
                [
                    {
                        user_id: userId,
                        answers: JSON.stringify(answers),
                        score,
                        resume_url: resumeUrl,
                        cover_letter_url: coverLetterUrl,
                    },
                ],
                { onConflict: ["user_id"], update: ["answers", "score", "resume_url", "cover_letter_url"] }
            );

        if (responseError) {
            console.error("Response upsert error:", responseError);
            return res.status(403).json({ error: "Failed to upsert response", details: responseError.message });
        }

        // Generate answers with questions for email
        const formattedAnswers = Object.keys(answers).map((key) => {
            const questionIndex = parseInt(key, 10);
            const question = questions[questionIndex]?.text || `Question ${parseInt(key) + 1}`;
            const answer = Array.isArray(answers[key]) ? answers[key].join(", ") : answers[key];
            return { question, answer };
        });

        // Send emails
        try {
            const transporter = nodemailer.createTransport({
                host: process.env.EMAIL_HOST,
                port: parseInt(process.env.EMAIL_PORT),
                secure: process.env.EMAIL_SECURE === "true",
                auth: {
                    user: process.env.EMAIL_USER,
                    pass: process.env.EMAIL_PASS,
                },
            });

            // Candidate confirmation email
            await transporter.sendMail({
                from: `"Growthpad Consulting Group" <${process.env.EMAIL_USER}>`,
                to: email,
                subject: "Thank You for Completing Your Interview!",
                html: `
                    <div style="font-family: Arial, sans-serif; max-width: 600px; margin: 0 auto; padding: 20px; color: #333;">
                        <img src="https://growthpad.co.ke/wp-content/uploads/2024/10/GCG-final-logo-proposals_v6-6.png" alt="Growthpad Logo" style="display: block; margin: 0 auto 20px; max-width: 150px;">
                        <h2 style="color: #f05d23; text-align: left;">Hello ${fullName},</h2>
                        <p style="font-size: 16px; line-height: 1.5;">
                            Thank you for taking the time to complete the Growthpad Consulting Group interview process! We appreciate your effort and commitment throughout this stage.
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            We're excited to let you know that we have received your submission, and your personality test score is <strong style="color: #f05d23;">${score}/190</strong>.
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            Our team will carefully review your application and your results. Based on your score and the insights we've gathered, we'll be in touch shortly regarding the next steps in the recruitment process. Please be patient as we ensure a thorough review.
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            We know that applying for a role can be a big decision, and we truly appreciate your interest in joining the Growthpad family. We’re looking for candidates who align with our core values, and we’re excited to explore how your skills and personality can contribute to our dynamic team.
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            If you have any questions or need additional information in the meantime, feel free to reach out to us at <a href="mailto:careers@growthpad.co.ke" style="color: #f05d23;">careers@growthpad.co.ke</a>. We’ll be happy to assist you.
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            Alternatively, you can reach us at:
                        </p>
                        <p style="font-size: 16px; line-height: 1.5;">
                            <strong>Growthpad Consulting Group</strong><br/>
                            7th Floor, Mitsumi Business Park,<br/>
                            Westlands – Nairobi, Kenya<br/>
                            P.O. Box 1093-00606<br/>
                            <strong>Phone:</strong> +254 701 850 850
                        </p>
                        <p style="font-size: 14px; text-align: center; color: #777; margin-top: 20px;">
                            Best regards,<br/>The Hiring Team<br/>Growthpad Consulting Group
                        </p>
                    </div>
                `,
            });

            // Admin notification email
            await transporter.sendMail({
                from: `"Growthpad Consulting Group" <${process.env.EMAIL_USER}>`,
                to: "analytics.growthpad@gmail.com",
                subject: `New Interview Submission from ${fullName}`,
                html: `
                    <div style="font-family: Arial, sans-serif; max-width: 600px; margin: 0 auto; padding: 20px; color: #333;">
                        <img src="https://growthpad.co.ke/wp-content/uploads/2024/10/GCG-final-logo-proposals_v6-6.png" alt="Growthpad Logo" style="display: block; margin: 0 auto 20px; max-width: 150px;">
                        <h2 style="color: #f05d23; text-align: center;">New Submission Received</h2>
                        <table style="width: 100%; border-collapse: collapse; margin: 20px 0;">
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Name:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${fullName}</td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Email:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${email}</td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Phone:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${phone || "N/A"}</td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">LinkedIn:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${linkedin ? `<a href="${linkedin}" style="color: #f05d23;">${linkedin}</a>` : "N/A"}</td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Score:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;"><strong>${score}/190</strong></td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Resume:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${resumeUrl ? `<a href="${resumeUrl}" style="color: #f05d23;">Download</a>` : "Not provided"}</td>
                            </tr>
                            <tr>
                                <td style="padding: 8px; font-weight: bold; border-bottom: 1px solid #ddd;">Cover Letter:</td>
                                <td style="padding: 8px; border-bottom: 1px solid #ddd;">${coverLetterUrl ? `<a href="${coverLetterUrl}" style="color: #f05d23;">Download</a>` : "Not provided"}</td>
                            </tr>
                        </table>
                        <h3 style="color: #333; margin-top: 20px;">Interview Answers</h3>
                        <table style="width: 100%; border-collapse: collapse;">
                            ${formattedAnswers
                    .map(
                        (qa, index) => `
                                        <tr style="background-color: ${index % 2 === 0 ? "#f9f9f9" : "#fff"};">
                                            <td style="padding: 10px; font-weight: bold; border-bottom: 1px solid #ddd;">${qa.question}</td>
                                            <td style="padding: 10px; border-bottom: 1px solid #ddd;">${qa.answer}</td>
                                        </tr>
                                    `
                    )
                    .join("")}
                        </table>
                    </div>
                `,
            });
        } catch (emailError) {
            console.error("Email sending error:", emailError.message);
        }

        return res.status(200).json({ message: "Submission successful", score });
    } catch (error) {
        console.error("Submission error:", error.message);
        return res.status(500).json({ error: "Internal server error", details: error.message });
    }
}

// Scoring logic (unchanged)
function calculateScore(answers) {
    let totalScore = 0;

    const q1Answers = answers[0] || [];
    if (q1Answers.includes("None")) totalScore += 0;
    else {
        const languageCount = q1Answers.length;
        totalScore += Math.min(5 + (languageCount - 1) * 2, 10);
    }

    const q2Answer = answers[1]?.[0];
    totalScore += q2Answer === "A" ? 10 : q2Answer === "B" ? 7 : q2Answer === "C" ? 4 : q2Answer === "D" ? 2 : 0;

    totalScore += answers[2]?.[0] === "Yes" ? 10 : 0;

    const q4Answer = answers[3]?.[0];
    totalScore +=
        q4Answer === "First class"
            ? 10
            : q4Answer === "Second class upper division"
                ? 7
                : q4Answer === "Second class lower division"
                    ? 4
                    : q4Answer === "Third class"
                        ? 2
                        : 0;

    const q5Answer = answers[4]?.[0];
    totalScore += q5Answer === "Both" ? 10 : q5Answer ? 5 : 0;

    const q6Answer = answers[5]?.[0];
    totalScore +=
        q6Answer === "Proficient" ? 10 : q6Answer === "Intermediate" ? 7 : q6Answer === "Beginner" ? 3 : 0;

    const q7Answer = answers[6]?.[0];
    totalScore +=
        q7Answer === "5+" ? 10 : q7Answer === "4" ? 8 : q7Answer === "3" ? 6 : q7Answer === "2" ? 4 : q7Answer === "1" ? 2 : 0;

    const q8Answer = answers[7]?.[0];
    totalScore +=
        q8Answer === "Listen to the customer's feedback and tell them that you can understand why they are upset and that it must be very inconvenient for them."
            ? 10
            : q8Answer === "Apologize to the customer and ask them to hold while you contact the project manager to establish where she is."
                ? 7
                : q8Answer === "Apologize to the customer and say you will arrange for a re-scheduled appointment."
                    ? 4
                    : 2;

    const q9Answer = answers[8]?.[0];
    totalScore +=
        q9Answer === "Take some time to read the information provided with the product e.g., product briefs, catalog etc."
            ? 10
            : q9Answer === "Contact your supervisor and ask for a meeting to walk you through the products/services."
                ? 7
                : q9Answer === "Ask a colleague who has already read about the product to give you a brief summary of the information you need."
                    ? 4
                    : 2;

    const q10Answer = answers[9]?.[0];
    totalScore +=
        q10Answer === "“I am sorry to hear that. What is it about the service that disappointed you today?”"
            ? 10
            : q10Answer === "“I understand your frustration; this is an unexpectedly busy hour. We are doing our best.”"
                ? 7
                : q10Answer === "“I am really sorry you feel that way. Would you like to get the manager so that you can talk to him?”"
                    ? 4
                    : 2;

    const q11Answer = answers[10]?.[0];
    totalScore +=
        q11Answer === "Set a goal to increase productivity by 5% next month and provide an incentive."
            ? 10
            : q11Answer === "Speak openly to the team leaders - tell them that they perform well but can do better and ask them to improve."
                ? 7
                : q11Answer === "Give a motivational speech in which you focus on how competent the team leaders are."
                    ? 4
                    : 2;

    const q12Answer = answers[11]?.[0];
    totalScore +=
        q12Answer === "10+ years" ? 10 : q12Answer === "5-10 years" ? 8 : q12Answer === "3-5 years" ? 6 : q12Answer === "2-3 years" ? 4 : 2;

    const q13Answer = answers[12]?.[0];
    totalScore +=
        q13Answer === "6+" ? 10 : q13Answer === "5" ? 8 : q13Answer === "4" ? 6 : q13Answer === "3" ? 4 : q13Answer === "2" ? 2 : 0;

    const q14Answer = answers[13]?.[0];
    totalScore +=
        q14Answer === "Communicate with stakeholders to clarify priorities and delegate tasks."
            ? 10
            : q14Answer === "Work overtime to complete all tasks."
                ? 7
                : q14Answer === "Request deadline extensions."
                    ? 4
                    : 2;

    const q15Answer = answers[14]?.[0];
    totalScore +=
        q15Answer === "Listen to their concerns, present data, and seek alignment."
            ? 10
            : q15Answer === "Escalate the issue to senior management."
                ? 7
                : q15Answer === "Avoid confrontation to maintain harmony."
                    ? 4
                    : 2;

    totalScore += answers[15]?.[0] === "Yes" ? 10 : 0;
    totalScore += answers[16]?.[0] === "Yes" ? 10 : 0;

    const q18Answer = answers[17]?.[0];
    totalScore +=
        q18Answer === "6+ years" ? 10 : q18Answer === "3-5 years" ? 7 : q18Answer === "2-3 years" ? 5 : q18Answer === "1-2 years" ? 3 : 0;

    const q19Answer = answers[18]?.[0];
    totalScore +=
        q19Answer === "Assess the situation, gather input from stakeholders, and then act."
            ? 10
            : q19Answer === "Immediately take charge and implement a solution."
                ? 7
                : q19Answer === "Delegate the issue to a team member."
                    ? 4
                    : 2;

    return Math.min(totalScore, 190);
}